<template>
    <d2-container>
        <!-- 销售管理员工 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="24">
                    <el-alert :title="this.$route.query.name+' 的员工列表'" type="info" :closable="false">
                    </el-alert>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="name" label="名称" width="180">
            </el-table-column>
            <el-table-column label="角色" width="180">
                <template slot-scope="a">
                    <p>销售</p>
                </template>
            </el-table-column>
            <el-table-column prop="is_phone" label="手机号" width="180">
            </el-table-column>
            <el-table-column prop="email" label="邮箱">
            </el-table-column>
            <el-table-column label="图片">
                <template slot-scope="a">
                    <img :src="a.row.qrcode_address" alt="" style="width: 100px;">
                </template>
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <el-button type="primary" plain @click="show_goods(val.row)">查看工资</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 查看工资 -->
        <el-dialog :title="num" :visible.sync="centerDialogVisible" width="30%" center :before-close="clear">
            <el-form ref="form" :model="form" label-width="130px">
                <el-form-item label="开始时间">
                    <el-date-picker v-model="form.banner_image_address" style="width: 100%;" type="date" placeholder="选择开始时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="结束时间">
                    <el-date-picker v-model="form.banner_href_address" style="width: 100%;" type="date" placeholder="选择结束时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="工资" v-if="money!==''">
                    <el-alert :title="money" type="success" :closable="false">
                    </el-alert>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onSubmit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>
    </d2-container>

</template>

<script>
    import { show_market, show_money } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'market_child',
        data() {
            return {
                num: '查看工资',
                value3: '',
                money: '',
                checked: 0,
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    banner_image_address: '',
                    banner_href_address: '',
                    id: ''
                },
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            page(a) {//分页
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            show_goods(a) {//查看工资
                console.log(a);
                this.$set(this.form, "id", a.id)
                this.centerDialogVisible = true;
            },
            onSubmit() {//查看工资
                let par = qs.stringify(this.form)
                show_money(par).then(res=>{
                    console.log(res);
                    this.msg(res)
                    this.money = String(res.data) + "￥";
                })
            },
            clear() {//清楚时间
                this.centerDialogVisible = false;
                this.form = {
                    banner_image_address: '',
                    banner_href_address: '',
                    id: ''
                }
            },
            init_goods() {//初始化
                let par = {
                    page: this.pages,
                    limit: 10
                }
                let id = this.$route.query.id
                show_market(par, id).then(res => {
                    console.log(res);
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    return
                }
                Myerror(res, this)
            },
        },
        mounted() {
            this.init_goods()
        }
    }
</script>
<style lang="scss">
</style>